@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  font-family: 'Raleway', sans-serif;
  margin: 0;
  background-color: black;
  color: white;
}

.header{height:100px;display:flex;align-items:center}.header .logo{width:100px;height:auto}.header .logo img{width:100%}.header .site-title{font-size:2rem;padding-bottom:1rem}

.blog-post-card{max-width:300px;border-radius:12px;box-shadow:9px 8px 19px -8px rgba(0,0,0,0.25);background-color:#879095;margin:0px 20px}.blog-post-card img{width:100%;height:auto;border-radius:12px 12px 0 0}.blog-post-card .post-info{padding:.5rem}.blog-post-card .post-info .date{text-align:end}

.home-page{width:100%;background-color:#47555e;border-radius:12px;padding:1rem}.home-page .mission-statement-container{height:40vh;display:flex;justify-content:center;align-items:center}.home-page .mission-statement-container .mission-statement-text{font-size:4rem;margin:0;width:420px;padding-bottom:100px}.home-page .recent-posts{background-color:#b1b6ba;border-radius:12px;padding:1rem;display:flex;justify-content:center}

body{background-color:#050505}.app{display:flex;justify-content:center;margin:1%}.v{font-size:3rem}

